.about {
  max-width: 1600px;
  margin: 0 auto;
  padding: 100px 0 100px 0;
  font-size: 25px;
  white-space: pre-line;
}

.about p {
  margin: 0;
}

.top {
  padding: 0 0 0 20px;
  border-bottom: 1px solid white;
}

.title {
  font-size: 70px;
  font-weight: bold;
  color: #adf53e;
  margin-bottom: 120px;
}

.block-top-personal {
  display: flex;
  align-items: flex-end;
  margin: 0 0 10px 0;
}

.p-top-name {
  font-size: 50px;
  font-weight: bold;
  color: #adf53e;
}

.bottom {
  display: flex;
  flex-flow: column wrap;
  padding-top: 40px;
  max-width: 1500px;
  max-height: 500px;
}

.block-bottom {
  font-weight: bold;
  margin-bottom: 20px;
  width: fit-content;
  margin: 20px 0 30px 0;
  list-style: none;
}
.block-bottom p {
  font-size: 17px;
}
.block-bottom li {
  margin: 5px 0;
}

.block-bottom-contact {
  display: flex;
  margin: 0;
  padding: 50px 100px 0 0;
}
.innerCircle {
  position: relative;
  width: 30px;
  height: 30px;
  margin: 0 20px;
  border: 3px solid #adf53e;
  border-radius: 50%;
  background-color: #fff;
}

.innerCircle img,
.innerCircle a {
  height: inherit;
  width: inherit;
  border-radius: 50%;
}

.block-display-none {
  display: none;
  position: absolute;
  width: fit-content;
  padding: 8px;
  left: 0px;
  bottom: -50px;
  -webkit-border-radius: 8px;
  -moz-border-radius: 8px;
  border-radius: 8px;
  background: #000;
  color: #adf53e;
  font-size: 14px;
}

.block-display-none:after {
  position: absolute;
  bottom: 100%;
  left: 15px;
  top: -2px;
  width: 0;
  height: 0;
  margin-left: -10px;
  border: solid transparent;
  border-color: #000;
  border-bottom-color: #fff;
  border-width: 10px;
  pointer-events: none;
  content: " ";
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  z-index: -1;
}

.innerCircle:hover .block-display-none {
  display: block;
}

.innerCircle:hover {
  -webkit-box-shadow: 0 0 15px #adf53e;
  box-shadow: 0 0 15px #adf53e;
}

@media screen and (max-width: 767px) {
  .about {
    max-width: none;
  }
  .title {
    font-size: 60px;
  }
  .block-top-personal {
    display: block;
  }
  .bottom {
    display: block;
    padding: 20px 30px 0 0;
    max-width: 767px;
    max-height: none;
  }
  .block-bottom {
    font-weight: normal;
    margin-bottom: 20px;
    width: fit-content;
    margin: 20px 0 30px 0;
    list-style: none;
  }
  .block-bottom p {
    font-size: 16px;
  }
  .block-bottom-contact {
    display: flex;
    width: fit-content;
    margin: 0 auto;
    padding: 20px 0 0 0;
  }
}

@media screen and (min-width: 768px) and (max-width: 1023px) {
  .about {
    max-width: 767px;
  }
  .top {
    padding: 0 0 0 20px;
  }
  .bottom {
    display: block;
    padding: 20px 30px 0 0;
    max-width: 767px;
    max-height: none;
  }
  .block-bottom {
    font-weight: bold;
    margin-bottom: 20px;
    width: fit-content;
    margin: 20px 0 30px 0;
    list-style: none;
  }
  .block-bottom p {
    font-size: 16px;
  }
  .block-bottom-contact {
    display: flex;
    width: fit-content;
    margin: 0 auto;
    padding: 20px 0 0 0;
  }
}
